        box-shadow: 0 0 2px rgba(0, 0, 0, .2);
        z-index: 101;
        padding: 20px;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        max-height: 90%;
        overflow: auto;
        -webkit-overflow-scrolling: touch
    }
    .mod_layer:after {
        content: '';
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5)
    }
    .mod_tips {
        position: fixed;
        top: 50%;
        left: 50%;
        min-width: 120px;
        text-align: center;
        background-color: rgba(0, 0, 0, .8);
        -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, .2);
        box-shadow: 0 0 2px rgba(0, 0, 0, .2);
        z-index: 501;
        padding: 15px 20px;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        -webkit-border-radius: 3px;
        border-radius: 3px;
        color: #fff;
        white-space: nowrap
    }
