    .drop-btn {
        height: 4rem;
        width: 4rem;
        background-color: rgba(0, 0, 0, .4);
        border-radius: .5rem 0 0 .5rem;
        box-sizing: border-box;
        text-align: center;
        line-height: 4rem
    }
    .drop-btn>.iconfont {
        font-size: 3rem;
        color: #fff
    }
    .drop-open .drop-btn {
        background-color: rgba(0, 0, 0, .6)
    }
    .drop-open .drop-btn>.iconfont {
        color: #8c7fee
    }
    .drop-menu {
        position: absolute;
        min-width: 14rem;
        opacity: 0;
        z-index: -100;
        display: none;
        -webkit-transition: .3s ease-out;
        transition: .3s ease-out;
        -webkit-backface-visibility: hidden
    }
    .drop-top>.drop-menu {
        bottom: 100%;
        right: 0;
        padding-bottom: .8rem;
        margin-bottom: -2rem
    }
    .drop-open .drop-top>.drop-menu {
        opacity: 1;
        z-index: 100;
        display: block;
        margin-bottom: .2rem
    }
    .drop-top>.drop-menu:after {
        content: "";
        border-style: solid;
        border-width: .8rem .8rem 0 .8rem;
        border-color: #fff transparent transparent transparent;
        height: 0;
        position: absolute;
        right: 1.2rem;
        bottom: 1px;
        width: 0;
        -webkit-transform: rotate(360deg);
        z-index: 2
    }
    .drop-top>.drop-menu:before {
        content: "";
        border-style: solid;
        border-width: .8rem .8rem 0 .8rem;
        border-color: rgba(0, 0, 0, .15) transparent transparent transparent;
        height: 0;
        position: absolute;
        right: 1.2rem;
        bottom: 0;
        width: 0;
        -webkit-transform: rotate(360deg);
        z-index: 1
    }
    .drop-menu .divider {
        height: 1px;
        overflow: hidden;
        background-color: #e5e5e5;
        padding: 0
    }
    .drop-menu-inner {
        padding: .8rem .5rem;
        font-size: 1.3rem;
        border: 1px solid #ccc;
        border: 1px solid rgba(0, 0, 0, .15);
        border-radius: .3rem;
        background-color: #fff
    }
    .drop-menu-inner>li>a {
        display: block;
        padding: .8rem .5rem
    }
    .drop-menu-inner>li>a>.iconfont {
        font-size: 1.7rem;
        padding-right: .5rem;
        color: #8c7fee
    }
